
import React, { Component } from 'react';
import InputField from '../component/InputField';
import PrimaryButton from '../component/Button';
import RadioSelection from '../component/RadioSelection';
import ImageList from '../component/ImageList';
import { submitForm, ping } from '../service/google-sheet.service';
import Spinner from '../component/Spinner';
import Checkbox from '../component/Checkbox';
import OrderErrorPage from './OrderErrorPage';
import OrderSuccessPage from './OrderSuccessPage';
import AddressInfo from './AddressInfo';
import PaymentInfo from './PaymentInfo';
import pic1 from '../pic1.jpg';
import pic2 from '../pic2.jpg';
import pic3 from '../pic3.jpg';
import pic4 from '../pic4.jpg';
import pic5 from '../pic5.jpg';
import pic6 from '../pic6.jpg';

interface State {
  fields: { [key: string]: any};
  pending: boolean;
  orderSubmitted: boolean;
  orderError: boolean;
  agreedToTerms: boolean;
  showError: boolean;
}


const items = [
  {
    label: '招牌豆漿派(6吋) $480(優惠價$450)',
    key: 'soyPie',
    price: 450
  },
  {
    label: '整顆藍莓派(6吋) $480(優惠價$450)',
    key: 'blueberryPie',
    price: 450
  },
  {
    label: '肉桂蘋果派(6吋) $550',
    key: 'cinnamonApplePie',
    price: 550
  },
  {
    label: '紅豆芋泥派(6吋) $550',
    key: 'redBeanPie',
    price: 550
  },
  {
    label: '生巧克力派(6吋) $580(優惠價$550)',
    key: 'chocolatePie',
    price: 550
  },
];

const DELIVERY_METHOD = {
  PICKUP: 'pickup',
  DELIVERY: 'delivery'
}

const DELIVERY_METHOD_LABEL = {
  [DELIVERY_METHOD.PICKUP]: '自取',
  [DELIVERY_METHOD.DELIVERY]: '冷凍宅配 貨到付款',
}

const DELIVERY_PRICE = 225;

const deliveryMethodOptions = [
  {
    value: DELIVERY_METHOD.PICKUP,
    label: DELIVERY_METHOD_LABEL[DELIVERY_METHOD.PICKUP],
  },
  {
    value: DELIVERY_METHOD.DELIVERY,
    label: DELIVERY_METHOD_LABEL[DELIVERY_METHOD.DELIVERY] + ' ($' + DELIVERY_PRICE + ')',
  }
]

class CheeseCakeOrderPage extends Component <{}, State> {
  constructor(props: any) {
    super(props);
    this.state = {
      fields: {
        deliveryMethod: DELIVERY_METHOD.PICKUP,
        name: '',
        phone: '',
        address: '',
      },
      pending: false,
      orderSubmitted: false,
      orderError: false,
      agreedToTerms: false,
      showError: false,
    };

    items.forEach((item) => {
      this.state.fields[item.key] = '';
    });

    // Ping server to wake up
    ping();
  }

  validate() {
    return this.state.fields.name && this.state.fields.phone && this.state.agreedToTerms && this.calculateTotalCost() > 0;
  }

  order = async () => {
    const valid = this.validate();
    if (!valid) {
      this.setState({
        showError: true
      });
      return;
    }


    this.setState({
      pending: true
    });

    try {
      await submitForm({
        name: this.state.fields.name || '',
        phone: this.state.fields.phone || '',
        // deliveryMethod: DELIVERY_METHOD_LABEL[this.state.fields.deliveryMethod],
        // address: this.state.fields.address || '',
        soyPie: this.state.fields.soyPie || '0',
        blueberryPie: this.state.fields.blueberryPie || '0',
        cinnamonApplePie: this.state.fields.cinnamonApplePie || '0',
        redBeanPie: this.state.fields.redBeanPie || '0',
        chocolatePie: this.state.fields.chocolatePie || '0',
        totalCost: String(this.calculateTotalCost()),
      });
    } catch (e) {
      // TODO: handle error
      this.setState({
        pending: false,
        orderError: true,
        orderSubmitted: false,
      });
      return;
    }

    this.setState({
      pending: false,
      orderError: false,
      orderSubmitted: true,
    });
    return;
  }

  setFieldValue = (fieldName: string) => {
    return (value: string) => {
      this.setState({
        fields: {
          ...this.state.fields,
          [fieldName]: value
        }
      });
    }
  }

  renderTextInput(field: string, label: string) {
    return (
      <div>
        <InputField
          key={field}
          style={{ width: '100%', maxWidth: 300 }}
          value={this.state.fields[field]}
          onChange={this.setFieldValue(field)}
          label={label}
        />
      </div>
    );
  }

  renderItemField(item: { label: string, key: string, price: number }) {
    return (
      <div key={item.key}>
        <div style={{ display: 'inline-block', lineHeight: '80px', fontSize: '20px', paddingRight: '30px', width: 350, maxWidth: '100%' }}>
          {item.label}
        </div>
        <InputField
          style={{ width: 100 }}
          isPositiveNumeric={true}
          type="number"
          value={this.state.fields[item.key]}
          onChange={this.setFieldValue(item.key)}
          label="數量"
        />
      </div>
    );
  }

  calculateTotalCost() {
    let totalPrice = 0;
    items.forEach((item) => {
      const amount = Number(this.state.fields[item.key]);
      if (!!amount) {
        totalPrice = totalPrice + (item.price * amount);
      }
    });

    // Calculate delivery price
    // if (this.state.fields.deliveryMethod === DELIVERY_METHOD.DELIVERY) {
    //   totalPrice = totalPrice + DELIVERY_PRICE;
    // }
    return totalPrice;
  }

  checkedTerms = (checked: boolean) => {
    this.setState({
      agreedToTerms: checked
    });
  }

  render() {
    if (this.state.orderError) {
      return <OrderErrorPage />;
    } else if (this.state.orderSubmitted) {
      return <OrderSuccessPage />;
    }

    return (
      <div>
        {
          (this.state.pending) ? <Spinner/> : ''
        }
        <ImageList images={[
          { img: pic1 },
          { img: pic2 },
          { img: pic3 },
          { img: pic4 },
          { img: pic5 },
          { img: pic6 }
        ]}/>

        <h2>
          線上訂購
        </h2>

        {this.renderTextInput('name', '姓名')}
        {
          (this.state.showError && !this.state.fields.name) ?
          <div className="error">請輸入姓名</div> : ''
        }
        {this.renderTextInput('phone', '手機')}
        {
          (this.state.showError && !this.state.fields.name) ?
          <div className="error">請輸入手機</div> : ''
        }
        {
          items.map((item) => {
            return this.renderItemField(item);
          })
        }

        {/* <RadioSelection
          label="交貨方式"
          options={deliveryMethodOptions}
          onChange={this.setFieldValue('deliveryMethod')}
          value={this.state.fields.deliveryMethod}
        /> */}
        {
          (this.state.showError && this.calculateTotalCost() < 1) ?
          <div className="error">請選擇至少一個商品</div> : ''
        }
        <div style={{ lineHeight: '80px', fontSize: '20px', fontWeight: 700 }}>
          總價: ${this.calculateTotalCost()}
        </div>


        {
          // (this.state.fields.deliveryMethod === DELIVERY_METHOD.DELIVERY) ?
          //   this.renderTextInput('address', '地址') :
          //   ''
        }
        <AddressInfo/>
        <PaymentInfo/>

        <div className="info-title">
          購買須知:
        </div>
        <div className="info-section">
          <div>
            1. 目前僅接受店面自取
          </div>
          <div>
            2. 若訂單金額超過兩千，需先匯款付帳
          </div>
          <div>
            3. 以上甜派皆為新鮮手作，保留期限約為兩週(冷凍)/一週(冷藏)
          </div>
          <div>
            4. 為確保商品新鮮，請在取貨時間2天內前來取貨
          </div>
        </div>

        <div>
          <Checkbox checked={this.state.agreedToTerms} onChange={this.checkedTerms}/>
          我同意以上購買說明
        </div>
        {
          (this.state.showError && !this.state.agreedToTerms) ?
          <div className="error">請同意購買說明</div> : ''
        }

        <div>
          <PrimaryButton label="訂購" onClick={this.order}/>
        </div>
      </div>
    );
  }
}


export default CheeseCakeOrderPage;
